/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { logout } from 'src/store/actions/authActions'

const useAutoLogout = () => {
  const dispatch = useDispatch()
  const auth = useSelector((state) => state.auth?.auth)
  const navigate = useNavigate()
  const [timer, setTimer] = useState(null)
  const authLogout = () => {
    dispatch(logout())
    clearAutoLogout()
    navigate('/login')
  }

  const autoLog = () => {
    if (localStorage.getItem('user') === null) {
      authLogout()
    } else {
      let expire_at = new Date(JSON.parse(localStorage.getItem('user')).expire_at).getTime() ?? 0
      let date = new Date().getTime()
      if (date >= expire_at) {
        authLogout()
      } else {
        setTimer(setTimeout(authLogout, expire_at - date))
      }
    }
  }
  const clearAutoLogout = () => {
    clearTimeout(timer)
  }
  useEffect(() => {
    autoLog()
  }, [auth])
  return [autoLog, clearAutoLogout]
}

export default useAutoLogout
