import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))
const NCBF = React.lazy(() => import('./views/ncbf_file/ncbf_file'))
const Typography = React.lazy(() => import('./views/theme/typography/Typography'))
const EPOC = React.lazy(() => import('./views/epoc_file/epoc_file'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard },
  { path: '/tools', name: 'Tools', exact: true },
  { path: '/tools/ncbf', name: 'Convert NCBF', element: NCBF },
  { path: '/tools/epoc', name: 'Convert EPOC', element: EPOC },
]

export default routes
